@use "../../utilities/variables";

.ucla-field__checkbox-label {
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  position: relative;
  line-height: 1.5;

  &[disabled] {
    color: variables.$disabled-state;
    cursor: not-allowed;
  }

  .ucla-field__checkbox {
    cursor: pointer;
    width: 1.5rem;
    height: 1.5rem;
    margin-right: 0.5rem;
    border: 1px solid variables.$grey-60;
    border-radius: 2px;

    &:disabled {
      background-color: variables.$grey-05;
      border-color: variables.$disabled-state;
      cursor: not-allowed;
    }

    &:not(:disabled):hover {
      border: 2px solid variables.$hover-state;
    }
    &:focus {
      border: 2px solid variables.$focus-state;
    }

    &:not(:disabled):active {
      background-color: variables.$active-state;

      &:checked {
        background-image: url("data:image/svg+xml,%3Csvg width='17' height='13' viewBox='0 0 17 13' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.7 12.025L-1.1059e-06 6.325L1.425 4.9L5.7 9.175L14.875 2.60083e-06L16.3 1.425L5.7 12.025Z' fill='%23FFFFFF'/%3E%3C/svg%3E");
        background-color: variables.$active-state;
      }
    }

    &:checked {
      background-image: url("data:image/svg+xml,%3Csvg width='17' height='13' viewBox='0 0 17 13' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.7 12.025L-1.1059e-06 6.325L1.425 4.9L5.7 9.175L14.875 2.60083e-06L16.3 1.425L5.7 12.025Z' fill='%232774AE'/%3E%3C/svg%3E");
      background-repeat: no-repeat;
      background-color: variables.$white;
      background-position: 50%;
    }

    &.ucla-is-warning {
      border: 2px solid variables.$warning-orange;
    }

    &.ucla-is-error {
      border: 2px solid variables.$error-red;
    }
  }

  .ucla-is-error & {
    .ucla-field__checkbox {
      border: 2px solid variables.$error-red;
    }
  }
  .ucla-is-warning & {
    .ucla-field__checkbox {
      border: 2px solid variables.$warning-orange;
    }
  }
}
