@use "../../utilities/variables";
@use "../../utilities/mixins";

.ucla-header--school {
  border-top: 11px solid variables.$ucla-blue;
  border-bottom: 1px solid variables.$grey-20;

  &__logo-link {
    line-height: 0;

    @include mixins.for-size(up-to-tablet) {
      margin-left: 1.5rem;
    }
    & > svg {
      width: 6rem;
      height: 2.75rem;

      @include mixins.for-size(tablet-up) {
        width: 7.688rem;
        height: 3.563rem;
      }
    }
  }

  &__site-title-link {
    line-height: 20px;
    margin-left: 0.375rem;
    font-size: 1.188rem;
    font-weight: bold;
    overflow-wrap: break-word;
    color: variables.$grey-80;

    &:hover {
      color: variables.$hover-state;
    }

    &:focus {
      color: variables.$focus-state;
    }

    @include mixins.for-size(phone-only) {
      inline-size: 106px;
    }

    @include mixins.for-size(tablet-up) {
      margin-left: 0.625rem;
      font-size: 2rem;
    }
  }

  .ucla-header__container {
    padding: 1rem 0;
    justify-content: flex-start;

    @include mixins.for-size(tablet-up) {
      padding: 0.938rem 0;
    }
    @include mixins.for-size(desktop-up) {
      padding: 0.938rem 1.5rem;

      &:has(.ucla-main-nav) {
        padding-bottom: 0;
      }
    }
  }

  .hamburger {
    margin-left: auto;
  }
}

// SVG Logo
.ucla-header--school__site-title-link > svg {
  max-height: 2.5rem;
  margin-top: 7px;

  @include mixins.for-size(phone-only) {
    &.ucla-header--school__site-title-link-desktop {
      display: none;
    }
  }

  @include mixins.for-size(tablet-up) {
    &.ucla-header--school__site-title-link-mobile {
      display: none;
    }
  }
}
