@use "../../utilities/variables";

.ucla-field__select {
  display: block;
  width: 100%;
  background-color: variables.$grey-05;
  border: 1px solid variables.$grey-60;
  border-radius: 2px;
  padding: 0.563rem 3rem 0.563rem 1rem;
  cursor: pointer;

  background-image: url("data:image/svg+xml,%3Csvg width='12' height='8' viewBox='0 0 12 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12 1.41L10.59 -6.16331e-08L6 4.58L1.41 -4.62904e-07L-6.16331e-08 1.41L6 7.41L12 1.41Z' fill='%23333333'/%3E%3C/svg%3E");
  background-repeat: no-repeat;
  background-position: right 1.375rem center;
  background-size: 0.8rem;

  &:disabled {
    color: variables.$grey-20;
    border-color: variables.$grey-05;
    cursor: not-allowed;
  }

  &:focus {
    outline: 2px solid variables.$focus-state;
  }

  .ucla-is-warning &,
  &.ucla-is-warning {
    border-color: variables.$warning-orange;
  }

  .ucla-is-error &,
  &.ucla-is-error {
    border-color: variables.$error-red;
  }
}
