@use "../../utilities/variables";
@use "../../utilities/mixins";

.ucla-side-navigation {
  border-bottom: 1px solid variables.$grey-20;

  @include mixins.for-size(up-to-tablet) {
    display: none;
  }
}

.ucla-side-navigation {
  &__list {
    list-style: none;
    margin: 0;
    border-top: 1px solid variables.$grey-20;

    &-item {
      margin-top: 0;
      &:not(:last-child) {
        border-bottom: 1px solid variables.$grey-20;
      }

      &--current > .ucla-side-navigation__link {
        border-color: variables.$ucla-gold;
      }
    }

    .ucla-side-navigation__list {
      list-style: none;

      .ucla-side-navigation__list-item > .ucla-side-navigation__link {
        padding-left: 2rem;
        background-color: variables.$grey-05;

        &:hover {
          color: variables.$white;
          background-color: variables.$hover-state;
        }
        &:active {
          color: variables.$white;
          background-color: variables.$active-state;
        }
      }

      .ucla-side-navigation__list {
        & > .ucla-side-navigation__list-item > .ucla-side-navigation__link {
          padding-left: 3rem;
          background-color: variables.$grey-10;

          &:hover {
            color: variables.$white;
            background-color: variables.$hover-state;
          }
          &:active {
            color: variables.$white;
            background-color: variables.$active-state;
          }
        }
      }
    }
  }

  &__link {
    display: block;
    font-weight: bold;
    line-height: 1.25;
    color: variables.$default-state;
    border-left: 4px solid transparent;
    padding-top: 0.75rem;
    padding-bottom: 0.75rem;
    padding-left: 0.75rem;

    &:hover {
      color: variables.$white;
      background-color: variables.$hover-state;
    }
    &:active {
      color: variables.$white;
      background-color: variables.$active-state;
    }
  }
}
