@use "../utilities/mixins";
@use "../utilities/variables";

.ucla-breadcrumb {
  font-size: 1rem;
  white-space: nowrap;

  .ucla-breadcrumb__link {
    align-items: center;
    color: variables.$default-state;
    font-weight: bold;
    display: flex;
    justify-content: center;
    cursor: pointer;
    padding: 0 8px;

    &:hover {
      background-color: variables.$hover-state;
      color: variables.$white;
    }

    &:active {
      background-color: variables.$active-state;
      color: variables.$white;
    }

    &:focus {
      outline: 1px solid variables.$focus-state;
      color: variables.$focus-state;
    }
  }

  .ucla-breadcrumb__list-item {
    align-items: center;
    display: flex;

    &:first-child .ucla-breadcrumb__link {
      padding-left: 0;
    }

    & + .ucla-breadcrumb__list-item {
      margin-top: 0;

      &::before {
        color: variables.$grey-40;
        content: "/";
      }
    }
  }

  .ucla-breadcrumb__list {
    align-items: flex-start;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin: 0;
  }
}
