@use "../../utilities/variables";
@use "../../utilities/mixins";

body {
  overflow-x: hidden;

  @include mixins.for-size(desktop-up) {
    overflow-x: unset;
  }
}

.ucla-header--college {
  border-bottom: 1px solid variables.$grey-20;
}

.ucla-header__logo {
  padding-top: 0.875rem;
  padding-bottom: 1rem;
  display: flex;
  align-items: flex-end;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  max-width: 76.5rem;
  margin: 0 auto;

  &-container {
    background-color: variables.$ucla-blue;
  }

  & > a {
    height: 20px;
    width: 61.71px;

    &:hover,
    &:focus {
      background-color: transparent;
    }
  }
}

.ucla-header__container {
  padding-bottom: 1.5rem;

  &:has(.ucla-main-nav) {
    padding-bottom: 1rem;

    @include mixins.for-size(desktop-up) {
      padding-bottom: 0;
    }
  }

  .ucla-header__site-name {
    @include mixins.for-size(up-to-tablet) {
      margin-left: 1.5rem;
    }
    > a {
      font-size: 1.75rem;
      font-weight: bold;
      text-decoration: none;
      color: variables.$grey-80;
      @include mixins.for-size(desktop-up) {
        font-size: 2.25rem;
      }

      &:hover,
      &:focus {
        background-color: transparent;
        color: variables.$grey-80;
      }
    }
  }
}
