@use "../utilities/variables";
@use "../utilities/mixins";

.ucla-dept-footer {
  background-color: variables.$ucla-blue;

  a:not([class]) {
    color: variables.$white;
    text-decoration: underline;

    &:visited {
      color: variables.$visited-state-light;
    }

    &:hover {
      color: variables.$black;
      background-color: variables.$hover-state-light;
    }
    &:focus {
      outline: 1px solid variables.$focus-state-light;
    }
    &:active {
      color: variables.$active-state-light;
    }
  }
}

.ucla-dept-footer__container {
  padding: 2rem 1.5rem;
  max-width: 76.5rem;
  margin: 0 auto;

  @include mixins.for-size(desktop-up) {
    padding-top: 4rem;
    padding-bottom: 4rem;
  }
}

.ucla-dept-footer__logo {
  width: 78px;
  height: 26px;

  @include mixins.for-size(desktop-up) {
    width: 108px;
    height: 35px;
  }
}

.ucla-dept-footer__info {
  margin-top: 1.5rem;

  @include mixins.for-size(desktop-up) {
    margin-top: 2rem;
  }
  .ucla-dept-footer__info-address {
    color: variables.$white;
    font-size: 0.875rem;
    font-weight: bold;
    line-height: 1.714;

    & > a {
      color: variables.$white;
      text-decoration: none;
    }
  }
}
.ucla-dept-footer__body {
  display: flex;
  flex-direction: column;

  @include mixins.for-size(desktop-up) {
    flex-direction: row;
    justify-content: space-between;
  }
}
.ucla-social {
  display: flex;
  flex-wrap: nowrap;
  list-style: none;

  .ucla-dept-footer & {
    margin-left: -0.75rem;
    margin-top: 1.375rem;
    margin-bottom: 1.375rem;

    @include mixins.for-size(desktop-up) {
      margin-bottom: 0;
      margin-top: 1.875rem;
    }
  }

  &__item {
    margin: 0;
  }

  &__link {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 44px;
    height: 44px;
    border-radius: 4px;

    & > svg {
      width: 20px;
      height: 20px;

      & > path {
        fill: variables.$white;
      }
    }

    &:hover {
      background-color: variables.$hover-state-light;
      & > svg > path {
        fill: variables.$black;
      }
    }
  }
}

.ucla-dept-footer__tertiary-nav {
  display: flex;
  flex-wrap: wrap;

  @include mixins.for-size(desktop-up) {
    flex-wrap: nowrap;
  }

  .ucla-dept-footer__nav-column {
    width: 50%;
    margin-bottom: 2rem;

    @include mixins.for-size(tablet-up) {
      width: 25%;
    }

    @include mixins.for-size(desktop-up) {
      width: unset;
      margin-left: 2.5rem;
      margin-bottom: 0;
    }

    .tertiary-nav-header {
      font-size: 0.75rem;
      font-weight: bold;
      color: variables.$white;
      margin-bottom: 8px;
    }
    .ucla-dept-footer__nav {
      list-style: none;
      margin: 0;
      li {
        margin-top: 0;
        margin-bottom: 8px;
        a {
          font-size: 0.75rem;
          text-decoration: none;
        }
      }
    }
  }
}

.ucla-global-footer {
  background-color: variables.$darker-blue;

  a {
    color: variables.$white;

    &:visited {
      color: variables.$visited-state-light;
    }

    &:hover {
      color: variables.$black;
      background-color: variables.$hover-state-light;
    }
    &:focus {
      outline: 1px solid variables.$focus-state-light;
    }
    &:active {
      color: variables.$active-state-light;
    }
  }

  .ucla-social {
    margin-top: 0;
    margin-left: -0.75rem;
    order: 3;
    @include mixins.for-size(tablet-up) {
      margin-left: 0;
      order: 2;
    }
    @include mixins.for-size(desktop-up) {
      order: 3;
    }
  }
}

.ucla-global-footer__container {
  padding: 1rem 1.5rem 0.375rem;
  max-width: 76.5rem;
  margin: 0 auto;
  display: flex;
  color: variables.$white;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;

  @include mixins.for-size(tablet-up) {
    padding-bottom: 1rem;
  }

  @include mixins.for-size(desktop-up) {
    flex-wrap: nowrap;
    padding-top: 0.375rem;
    padding-bottom: 0.375rem;
  }
}

.ucla-global-footer__copyright,
.ucla-global-footer__link {
  font-size: 0.8rem;
}

.ucla-global-footer__copyright {
  order: 1;
  white-space: normal;
  @include mixins.for-size(desktop-up) {
    white-space: nowrap;
  }

  > a {
    font-weight: bold;
  }
}

.ucla-global-footer__list {
  order: 2;
  display: flex;
  width: 100%;
  flex-direction: column;
  margin-top: 1rem;
  margin-left: 0;
  list-style: none;

  li {
    margin-top: 0;
    margin-bottom: 1rem;

    @include mixins.for-size(tablet-up) {
      margin-bottom: 0;
    }
  }

  @include mixins.for-size(tablet-up) {
    order: 3;
    flex-grow: 1;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    margin-top: 0;

    > li {
      margin-right: 2.62rem;
    }
  }

  @include mixins.for-size(desktop-up) {
    order: 2;
    justify-content: space-evenly;

    > li {
      margin-right: 0;
    }
  }
}
