@use "../../utilities/variables";

.ucla-field__input {
  background-color: variables.$grey-05;
  border: 1px solid variables.$grey-60;
  border-radius: 4px;
  width: 100%;
  max-width: 100%;
  padding: 0.563rem 1rem;

  &::placeholder {
    color: variables.$grey-40;
  }

  &:focus {
    outline: 2px solid variables.$focus-state;
  }

  .ucla-is-warning &,
  &.ucla-is-warning {
    border-color: variables.$warning-orange;
  }

  .ucla-is-error &,
  &.ucla-is-error {
    border-color: variables.$error-red;
  }

  .ucla-has-left-icon & {
    padding-left: 3em;
  }
  .ucla-has-right-icon & {
    padding-right: 3em;
  }

  &:disabled {
    color: variables.$grey-20;
    border-color: variables.$grey-05;
    cursor: not-allowed;
  }
}

.ucla-field__icon-left,
.ucla-field__icon-right {
  pointer-events: none;
  position: absolute;
  top: 0;
  z-index: 2;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 3.5em;
  height: 2.4em;

  & > svg {
    width: 20px;
    height: 16px;
  }
}
.ucla-field__icon-left {
  left: 0;
}
.ucla-field__icon-right {
  right: 0;
}
