@use "../utilities/variables";
@use "../utilities/mixins";

.ucla-callout {
  padding: 0.75rem 1.5rem 0.75rem 1.25rem;
  font-size: 1rem;
  line-height: 1.25;
  font-weight: bold;
  border-left: 4px solid variables.$ucla-gold;
  display: flex;
  flex-direction: column;

  @include mixins.for-size(tablet-up) {
    padding: 1rem 2rem 1rem 1.75rem;
    font-size: 1.25rem;
    line-height: 1.2;
  }

  @include mixins.for-size(tablet-up) {
    flex-direction: row;
    align-items: center;
  }

  .ucla-callout__number {
    font-size: 2.5rem;
    margin-bottom: 4px;

    @include mixins.for-size(tablet-up) {
      margin-bottom: 0;
      margin-right: 1rem;
      font-size: 3.375rem;
    }
  }
  .ucla-callout__body {
    display: flex;
    flex-direction: column;
    font-size: 1rem;

    @include mixins.for-size(tablet-up) {
      font-size: 1.25rem;
    }

    cite {
      font-size: 0.875rem;
      margin-top: 4px;
      color: variables.$grey-40;
    }
  }
}
