.ucla-img-responsive {
  max-width: 100%;
  height: auto;
}

.ucla-img-rounded {
  border-radius: 9999px;
  width: 100%;
  height: auto;
  display: block;
}

.ucla-img-ratio {
  display: block;
  position: relative;

  img {
    display: block;
    height: auto;
    width: 100%;
    bottom: 0;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
  }

  &.ucla-img-ratio-1x1 {
    padding-top: 100%;
  }
  &.ucla-img-ratio-3x2 {
    padding-top: 66.6666%;
  }
  &.ucla-img-ratio-4x3 {
    padding-top: 75%;
  }
  &.ucla-img-ratio-2x3 {
    padding-top: 150%;
  }
  &.ucla-img-ratio-3x4 {
    padding-top: 133.3333%;
  }
  &.ucla-img-ratio-16x9 {
    padding-top: 56.25%;
  }
}
