@use "../utilities/variables";

.ucla-tabslist {
  display: flex;
  border-bottom: 1px solid variables.$grey-20;
}

.ucla-tablink {
  display: inline-block;
  text-align: center;
  font-size: 1rem;
  font-weight: bold;
  color: variables.$default-state;
  padding: 0.75rem 1.5rem 0.5rem;
  cursor: pointer;
  border-bottom: 4px solid transparent;

  &.is-active {
    border-bottom: 4px solid variables.$ucla-gold;
    color: variables.$grey-80;
  }

  &:hover {
    border-bottom: 4px solid variables.$hover-state;
  }
  &:active {
    border-bottom: 4px solid variables.$active-state;
  }
}

.ucla-tabpanels {
  padding-top: 1rem;
  padding-bottom: 1rem;
}
