@use "../utilities/variables";

.ucla-carousel {
  .ucla-carousel__pagination {
    position: unset;
  }
  .ucla-carousel__page {
    background: variables.$grey-20;
    width: 10px;
    height: 10px;
    opacity: unset;

    &.is-active {
      background: variables.$active-state;
      scale: unset;
      transform: unset;
    }

    &:hover {
      background: variables.$hover-state;
    }

    &:focus {
      outline: 1px solid variables.$focus-state;
      outline-offset: 1px;
    }
  }
}
